import React, { useState, useEffect } from 'react';
import { useRive, Layout, Fit, Alignment, useStateMachineInput, EventType, RiveEventType } from '@rive-app/react-canvas';
import { gsap } from 'gsap'; // Import GSAP

const ElectoralVotes = () => {
    // Data for the drop-down menu (mapstr values)
    const mapOptions = {
        "national": "National",
        "arizona": "Arizona",
        "georgia": "Georgia",
        "michigan": "Michigan",
        "pennsylvania": "Pennsylvania",
        "wisconsin": "Wisconsin"
    };

    // State to hold the selected map and the electoral data
    const [selectedMap, setSelectedMap] = useState("national"); // Default selection
    const [votes, setVotes] = useState({ harris: 0, trump: 0, state: "" });

    // Fetch data from backend whenever selectedMap changes
    useEffect(() => {
        const fetchData = async () => {
            try {
                const response = await fetch(`https://981e-175-99-132-74.ngrok-free.app/api/electoral-votes?state=${selectedMap}`, {
                    headers: {
                        'ngrok-skip-browser-warning': 'true'
                    }
                });

                if (!response.ok) {
                    throw new Error('Network response was not ok');
                }
                const data = await response.json();

                // Parse Harris and Trump percentages as numbers (removing % sign)
                const harrisValue = parseFloat(data.harris.replace('%', ''));
                const trumpValue = parseFloat(data.trump.replace('%', ''));

                // Determine if Trump is winning BEFORE animation
                const newTrumpWin = trumpValue > harrisValue;
                if (isTrumpWin.value !== newTrumpWin) {
                    isTrumpWin.value = newTrumpWin; // Update Trump's win status immediately
                }

                // GSAP animation to transition harris value
                gsap.fromTo(votes,
                    { harris: 0, trump: 0 },
                    {
                        harris: harrisValue,
                        trump: trumpValue,
                        duration: 1.5, // Adjust the duration for smoothness
                        ease: "elastic.out(1, 0.3)", // Overshoot effect
                        onUpdate: () => {
                            setVotes((prevVotes) => ({
                                ...prevVotes,
                                harris: parseFloat(votes.harris.toFixed(0)), // Keep last two decimals
                                trump: parseFloat(votes.trump.toFixed(0)) // Keep last two decimals
                            }));

                        },
                        // onComplete: () => {
                        //     // Only update TrumpIsWin after the animation completes
                        //     const newTrumpWin = trumpValue > harrisValue;
                        //     if (isTrumpWin.value !== newTrumpWin) {
                        //         isTrumpWin.value = newTrumpWin;
                        //     }
                        // }
                    });

                setVotes({ harris: harrisValue, trump: trumpValue, state: data.state });
            } catch (error) {
                console.error('Error fetching electoral votes:', error);
            }
        };

        fetchData();
    }, [selectedMap]);

    const { rive, RiveComponent } = useRive({
        src: "2024_election.riv",
        // src: "circle.riv",
        stateMachines: "SM",
        autoplay: true,
        layout: new Layout({
            // fit: Fit.Contain,
            alignment: Alignment.Center,
        }),
    });

    const H_n = useStateMachineInput(rive, "SM", 'H_n', 0);
    const T_n = useStateMachineInput(rive, "SM", 'T_n', 0);
    const isTrumpWin = useStateMachineInput(rive, "SM", 'isTrumpWin', true); // Initialize as a boolean value

    const onRiveEventReceived = (riveEvent) => {
        const eventData = riveEvent.data;
        if (eventData.type === RiveEventType.General) {
            setSelectedMap(eventData.name);
        }
    };

    // Wait until the rive object is instantiated before adding the Rive event listener
    useEffect(() => {
        if (rive) {
            rive.on(EventType.RiveEvent, onRiveEventReceived);
        }
    }, [rive]);

    useEffect(() => {
        if (rive) {
            if (H_n && T_n) {
                H_n.value = votes.harris;
                T_n.value = votes.trump;
                // const newTrumpWin = votes.trump > votes.harris;
                // if (isTrumpWin.value !== newTrumpWin) {
                //     isTrumpWin.value = newTrumpWin;
                // }
                // isTrumpWin.value = true;
            }

            rive.setTextRunValue("D_num", votes.harris.toString());
            rive.setTextRunValue("R_num", votes.trump.toString());
        }
    }, [rive, votes.harris, votes.trump]);

    // Handle drop-down selection change
    const handleMapChange = (event) => {
        setSelectedMap(event.target.value);
    };

    return (
        <div className="container">
            {/* Drop-down menu (optional, can be removed if using Rive event) 

            <div className="dropdown-container">
                <select value={selectedMap} onChange={handleMapChange}>
                    {Object.keys(mapOptions).map((key) => (
                        <option key={key} value={key}>
                            {mapOptions[key]}
                        </option>
                    ))}
                </select>
            </div>*/}
            {/* Rive component */}
            <div className="rive-container">
                <RiveComponent />
            </div>
        </div>
    );
};

export default ElectoralVotes;
